<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div id= "newItem" style="width: 800px; height: 50px; margin-left: 40px; margin-bottom: 10px;">
      <input id="inputbox"  placeholder="Add a new item" v-model="newitem"  @keyup.enter="addItem()"> 
      <input type = "button" id="btn" value="Add" style="height: 45px; width: 50px"  v-on:click="addItem()">
      <input type = "button" id="btn1" value="Reset" style="height: 45px; width: 55px; margin-left: 10px"  v-on:click="clearAll()">
    </div>
    <div id="updateBox">
      <input type="text" placeholder="Update item" id="updateitem"  v-model="updateditem"/>
      <input type = "button" id="btn2" value="Edit" style="height: 45px; width: 50px;" @click="editItem()"/>
      <input type = "button" id="btn3" value="Cancel" style="height: 45px; width: 55px; margin-left: 10px;" @click="cancel()" />
    </div>
    <ul>
      <li v-if="checked===true" style="background-color: lightblue; width: 800px; height: 50px; text-decoration: line-through;"  v-for="(todo, index) in todos" v-bind:key="index">
        <input type="checkbox" id="checkbox" v-model="checked" style="float: left;">
        <strong>{{ todo }}</strong>
        <span><input type="button" value="X"  v-on:click="deleteItem(index)"></span>
        <span><button><img src="http://www.iconarchive.com/download/i80229/custom-icon-design/flatastic-1/edit.ico" v-on:click="updateItem(index)" width="13" height="13"/></button></span> 
      </li>
      <li v-else style="background-color: lightblue; width: 800px; height: 50px;">
          <input type="checkbox" id="checkbox" v-model="checked" style="float: left;">
        <strong>{{ todo }}</strong>
        <span><input type="button" value="X"  v-on:click="deleteItem(index)"></span>
        <span><button><img src="http://www.iconarchive.com/download/i80229/custom-icon-design/flatastic-1/edit.ico" v-on:click="updateItem(index)" width="13" height="13"/></button></span> 
      </li>
      
    </ul>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  name: 'HelloWorld',
  props: {
    msg: String
    
  }
  ,
  data() {
    return {
      newitem: '',
      updateditem: '',
      updatingitem: '',
      itemindex: 0,
      todos: localStorage.getItem('items') ? JSON.parse(localStorage.getItem('items')) : [],
      checked: false
    
    }
  },
  methods:{
    addItem(){
      this.todos.push(this.newitem);
      console.log(this.newitem);
      this.newitem = '';
      console.log(this.todos);
      localStorage.setItem("items", JSON.stringify(this.todos));
      },

    deleteItem(index) {
      this.todos.splice(index,1);
      localStorage.setItem("items", JSON.stringify(this.todos));
      console.log(this.todos);
    },

    clearAll() {
      localStorage.removeItem("items");
      this.todos = [];
    },

    updateItem(index) {
      document.getElementById('updateBox').style.display = 'inline';
      console.log(this.todos[index]);
      this.updatingitem = this.todos[index];
      document.getElementById('updateitem').value = this.updatingitem;
      this.itemindex = (this.todos).indexOf(this.updatingitem);
      console.log("item index: "+this.itemindex);
    },

    editItem() {
      Vue.set(this.todos, this.itemindex, this.updateditem);
      localStorage.setItem("items", JSON.stringify(this.todos));
      document.getElementById("updateBox").style.display = 'none';
    },

    cancel() {
      document.getElementById('updateBox').style.display = 'none';
    }
  },
  
  
}

</script>

